@import url('https://fonts.googleapis.com/css2?family=Mulish&display=swap');

body {
  margin: 0;
  font-family: 'Mulish', sans-serif;
  position: relative;
  min-height:100vh;
}

body::after {
  content: '';
  display: block;
  height: 30px; /* Set same as footer's height */
}
.navbar {
  background-color: #fff;
}

.navbar-brand {
  font-size: 1.9rem;
  color: #565387;
}

.navbar img {
  height: 50px;
}
.navbar a {
  font-size: 1.1rem;
  text-transform: capitalize;
}

.navbar a:hover {
  color: #3498db !important;
}

.menu_active {
  border-bottom: 1px solid #565387;
}



/* ======================= Header Section=============================== */

#header {
  width: 100%;
  height: 80vh;
  margin-top: 15px;
}

#header h1 {
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: #000;
}

#header .brand-name {
  color: #3498db;
}

#header h2 {
  color: #484848;
  font-size: 24px;
}

#header .btn-get-started {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-transform: none;
  text-decoration: none;
}

#header .btn-get-started:hover {
  background: #3498db;
  color: #fff;
}
.animated {
  margin-top: -40px;
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@keyframes up-down {
  0% {
    transform: translateY(10px);
  } 100% {
    transform: translateY(-10px);
  }
}


/* ======================= social_media =============================== */
.social_media ul {
  display: flex;
  direction: rtl;
}
.social_media ul li {
  list-style: none;
  margin: 0 5px;
}


/* ======================= Footer =============================== */

footer {
  height: 30px;
  line-height: 30px;
  background-color: #3498db !important;
  margin-top: auto;
  position: absolute;
  bottom: 0;
  width: 100%;
}



